import { Component, ElementRef, ViewChild, OnInit } from '@angular/core';
import { NavController, Config } from 'ionic-angular';
import { HttpClient } from '@angular/common/http';
import L, { tileLayer } from 'leaflet';
import * as mapboxgl from 'mapbox-gl';
import {MapboxDirections } from '@mapbox/mapbox-gl-directions'
import {mbxDirections} from '@mapbox/mapbox-sdk/services/directions';
import { mbxStyles  } from "@mapbox/mapbox-sdk/services/styles";
@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage implements OnInit {
  @ViewChild('map') mapContainer: ElementRef;
  map: mapboxgl.Map;
  canvas: any;
  style = 'mapbox://styles/mapbox/outdoors-v9';
  lat = 37.75;
  lng = -122.41;
 //stylesService = mbxStyles({access_token:'pk.eyJ1Ijoicmlja2NhcmRkZGQiLCJhIjoiY2puYWd4cTU3MGc3azNycDh3dnllNWNtZyJ9.MTH5zn5hLiXz9jBvGadOVQ' });
 //directionsClient = mbxDirections({ accessToken: 'pk.eyJ1Ijoicmlja2NhcmRkZGQiLCJhIjoiY2puYWd4cTU3MGc3azNycDh3dnllNWNtZyJ9.MTH5zn5hLiXz9jBvGadOVQ' });
  constructor(public navCtrl: NavController, public http: HttpClient) {
    //mapboxgl.accessToken == 'pk.eyJ1Ijoicmlja2NhcmRkZGQiLCJhIjoiY2puYWd4cTU3MGc3azNycDh3dnllNWNtZyJ9.MTH5zn5hLiXz9jBvGadOVQ'

  }
  ngOnInit() {
    // this.markers = this.mapService.getMarkers()
    this.initializeMap()
  }

  private initializeMap() {
    /// locate the user
    if (navigator.geolocation) {
      navigator.geolocation.getCurrentPosition(position => {
        this.lat = position.coords.latitude;
        this.lng = position.coords.longitude;
        this.map.flyTo({
          center: [this.lng, this.lat]
        })
      });
    }

    this.buildMap()

  }

  buildMap() {
    this.map = new mapboxgl.Map({
      container: 'map',
      style: this.style,
      zoom: 13,
      center: [this.lng, this.lat]
    });
    this.map.on('click', (event) => {
      //const coordinates = [event.lngLat.lng, event.lngLat.lat];
      let cos = new mapboxgl.Marker({
        draggable: true
      })
        .setLngLat([event.lngLat.lng, event.lngLat.lat])
        .addTo(this.map);
      // const newMarker   = new GeoJson(coordinates, { message: this.message })
      //this.mapService.createMarker(newMarker)
    })
    this.map.on('load', this.getRoute);
  }

  getRoute() {
    // this.directionsClient.getDirections({
    //   waypoints: [
    //     {
    //       coordinates: [13.4301, 52.5109],
    //       approach: 'unrestricted'
    //     },
    //     {
    //       coordinates: [13.4265, 52.508]
    //     },
    //     {
    //       coordinates: [13.4194, 52.5072],
    //       bearing: [100, 60]
    //     }
    //   ]
    // })
    // .send()
    // .then(response => {
    //   const directions = response.body;
    // });
  }
  BackToStart() {
    this.navCtrl.pop();
  }



}



